.wrapper {
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	height: 100vh;
	position: relative;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.content {
	width: fit-content;
	height: fit-content;
	position: absolute;
	top: 35%;
	bottom: 12%;

	border-radius: 25px;
	background-color: #ffffff;
	padding-bottom: 30px;
	padding-left: 20px;
	padding-right: 20px;
}

.title {
	text-align: center;
	margin-bottom: 60px;

	span {
		color: #662adc;
		font-weight: 900;
	}
}

.convert-block {
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	align-items: center;
	column-gap: 30px;
}

.inverse {
	flex-direction: row-reverse;
}

.arrows {
	width: 40px;
	height: 40px;
	opacity: 0.6;
	transition: all ease-in 0.5s;
	cursor: pointer;
}

.arrows:hover {
	width: 40px;
	height: 40px;
	opacity: 1;
}

.convert-from {
	display: flex;
	flex-direction: column;
}
.convert-to {
	display: flex;
	flex-direction: column;
}

.convert {
	display: flex;
	flex-direction: column;
	select {
		height: 40px;
		width: 200px;
		border-radius: 8px;
		outline: none;
		margin-bottom: 10px;
		border: 1px solid #858585;
		transition: all ease-in 0.5s;
		padding: 0 10px 0 10px;

		option {
			background-color: #d8d8d8;
		}
	}
	select:focus {
		border: 1px solid #181818;
	}

	input {
		height: 40px;
		border-radius: 8px;
		outline: none;
		border: 1px solid #858585;
		transition: all ease-in 0.5s;
		padding: 0 15px 0 15px;

		input:focus {
			border: 1px solid #181818;
		}
	}
}

@media (max-width: 915px) {
	.title {
		font-size: 24px;
	}
}
